.color-picker {
  position: absolute;
  z-index: 100000;

  width: 230px;
  height: auto;
  border: #777 solid 1px;

  cursor: default;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #fff;

  & * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;

    font-size: 11px;
  }

  input {
    height: 26px;
    min-width: 0;

    font-size: 13px;
    text-align: center;
    color: #000;

    &:invalid {
      box-shadow: none;
    }

    &:-moz-ui-invalid {
      box-shadow: none;
    }

    &:-moz-submit-invalid {
      box-shadow: none;
    }

    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button {
      margin: 0;

      -webkit-appearance: none;
    }
  }

  .arrow {
    position: absolute;
    z-index: 999999;

    width: 0;
    height: 0;
    border-style: solid;

    &.arrow-top {
      left: 8px;

      border-width: 10px 5px;
      border-color: #777 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
    }

    &.arrow-left {
      top: 8px;
      left: 100%;

      border-width: 5px 10px;
      border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #777;
    }

    &.arrow-right {
      top: 8px;
      left: -20px;

      border-width: 5px 10px;
      border-color: rgba(0, 0, 0, 0) #777 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
    }

    &.arrow-bottom {
      top: -20px;
      left: 8px;

      border-width: 10px 5px;
      border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #777 rgba(0, 0, 0, 0);
    }
  }

  .cursor {
    position: relative;

    width: 16px;
    height: 16px;
    border: #222 solid 2px;
    border-radius: 50%;

    cursor: default;
  }

  .box {
    display: flex;
    padding: 4px 8px;

    .left {
      position: relative;

      padding: 16px 8px;
    }

    .right {
      -webkit-flex: 1 1 auto;
      -ms-flex: 1 1 auto;
      flex: 1 1 auto;

      padding: 12px 8px;
    }
  }

  .button-area {
    padding: 0 16px 16px;

    text-align: right;
  }

  .preset-area {
    padding: 4px 15px;

    .preset-label {
      overflow: hidden;
      width: 100%;
      padding: 4px;

      font-size: 11px;
      white-space: nowrap;
      text-align: left;
      text-overflow: ellipsis;
      color: #555;
    }

    .preset-color {
      position: relative;

      display: inline-block;
      width: 18px;
      height: 18px;
      margin: 4px 6px 8px;
      border: #a9a9a9 solid 1px;
      border-radius: 25%;

      cursor: pointer;
    }

    .preset-empty-message {
      min-height: 18px;
      margin-top: 4px;
      margin-bottom: 8px;

      font-style: italic;
      text-align: center;
    }
  }

  .hex-text {
    width: 100%;
    padding: 4px 8px;

    font-size: 11px;

    .box {
      padding: 0 24px 8px 8px;

      div {
        float: left;

        -webkit-flex: 1 1 auto;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;

        text-align: center;
        color: #555;
        clear: left;
      }

      input {
        -webkit-flex: 1 1 auto;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        padding: 1px;
        border: #a9a9a9 solid 1px;
      }
    }
  }

  .hsla-text,
  .rgba-text {
    width: 100%;
    padding: 4px 8px;

    font-size: 11px;

    .box {
      padding: 0 24px 8px 8px;

      div {
        -webkit-flex: 1 1 auto;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        margin-right: 8px;

        text-align: center;
        color: #555;

        &:last-child {
          margin-right: 0;
        }
      }

      input {
        float: left;

        -webkit-flex: 1;
        -ms-flex: 1;
        flex: 1;
        min-width: 0;
        padding: 1px;
        margin: 0 8px 0 0;
        border: #a9a9a9 solid 1px;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  .hue-alpha {
    margin-bottom: 3px;
  }

  .hue {
    direction: ltr;

    width: 100%;
    height: 16px;
    margin-bottom: 16px;
    border: none;

    cursor: pointer;
    background-size: 100% 100%;
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAJYAAAAQCAYAAAD06IYnAAAABmJLR0QA/wD/AP+gvaeTAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAB3RJTUUH4AIWDwkUFWbCCAAAAFxJREFUaN7t0kEKg0AQAME2x83/n2qu5qCgD1iDhCoYdpnbQC9bbY1qVO/jvc6k3ad91s7/7F1/csgPrujuQ17BDYSFsBAWwgJhISyEBcJCWAgLhIWwEBYIi2f7Ar/1TCgFH2X9AAAAAElFTkSuQmCC');
  }

  .alpha {
    direction: ltr;

    width: 100%;
    height: 16px;
    border: none;

    cursor: pointer;
    background-size: 100% 100%;
    background-image: url('data:image/png;base64,');
  }

  .type-policy {
    position: absolute;
    top: 218px;
    right: 12px;

    width: 16px;
    height: 24px;

    background-size: 8px 16px;
    background-image: url('data:image/png;base64,');
    background-repeat: no-repeat;
    background-position: center;
  }

  .selected-color {
    position: absolute;
    top: 16px;
    left: 8px;

    width: 40px;
    height: 40px;
    border: 1px solid #a9a9a9;
    border-radius: 50%;
  }

  .selected-color-background {
    width: 40px;
    height: 40px;
    border-radius: 50%;

    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACgAAAAoCAYAAACM/rhtAAAAh0lEQVRYR+2W0QlAMQgD60zdfwOdqa8TmI/wQMr5K0I5bZLIzLOa2nt37VVVbd+dDx5obgCC3KBLwJ2ff4PnVidkf+ucIhw80HQaCLo3DMH3CRK3iFsmAWVl6hPNDwt8EvNE5q+YuEXcMgkonVM6SdyCoEvAnZ8v1Hjx817MilmxSUB5rdLJDycZgUAZUch/AAAAAElFTkSuQmCC');
  }

  .saturation-lightness {
    direction: ltr;

    width: 100%;
    height: 130px;
    border: none;

    cursor: pointer;
    background-size: 100% 100%;
    background-image: url('data:image/png;base64,');
  }

  .cp-add-color-button-class {
    position: absolute;

    display: inline;
    padding: 0;
    margin: 3px -3px;
    border: 0;

    cursor: pointer;
    background: transparent;

    &:hover {
      text-decoration: underline;
    }

    &:disabled {
      cursor: not-allowed;
      color: #999;

      &:hover {
        text-decoration: none;
      }
    }
  }

  .cp-remove-color-button-class {
    position: absolute;
    top: -5px;
    right: -5px;

    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;

    cursor: pointer;
    text-align: center;
    background: #fff;

    box-shadow: 1px 1px 5px #333;

    &::before {
      content: 'x';

      position: relative;
      bottom: 3.5px;

      display: inline-block;

      font-size: 10px;
    }
  }
}
